import {LitElement, html} from 'lit';
import {customElement} from 'lit/decorators.js';



@customElement('mahjong-rule')
export class MahjongRule extends LitElement {
  override render() {
    return html`
        <h1>ルール</h1>
        <h2>四麻</h2>
        <p><a href="https://m-league.jp/about/" target="_blank">Mリーグのルール</a>に準拠します。</p>
    `;
  }
}

declare global {
    interface HTMLElementTagNameMap {
      'mahjong-rule': MahjongRule;
    }
  }